var tl=gsap.timeline()
tl
.from("#number4",{
    opacity:0,
    onStart:function(){
        $('#number4').textillate({
             in: { 
                effect: 'fadeInUp',
                   callback:function(){
                    $('#number4').textillate('out')
                }
            },
             out:{ effect: 'fadeOutUp'}
        });
    }
})
.from("#number3",{
    opacity:0,
    delay:1.1,
    onStart:function(){
        $('#number3').textillate({
             in: { effect: 'fadeInUp',
                   callback:function(){
                    $('#number3').textillate('out')
                   }
            },
             out:{ effect: 'fadeOutUp'}
        });
    }
})
.from("#number2",{
    opacity:0,
    delay:1.1,
    onStart:function(){
        $('#number2').textillate({
             in: { effect: 'fadeInUp',
                   callback:function(){
                    $('#number2').textillate('out')
                   }
            },
             out:{ effect: 'fadeOutUp'}
        });
    }
})
.from("#number1",{
    opacity:0,
    delay:1.1,
    onStart:function(){
        $('#number1').textillate({
             in: { effect: 'fadeInUp',},
        });
    }
})
.to(".screen",{
    top:"-100%",
    delay:1,
    duration:1.5,
    ease:"Power4.easeOut"
})
